.menu-item
{
    margin-right: 7.5px;
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center;
    background-size: cover;
    border: 1px solid black;
    overflow: hidden;
   
    &:hover {
        cursor: pointer;
      
        & .background-image {
          transform: scale(1.1);
          transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }
      
        & .content {
          opacity: 0.9;
          border: 1px solid;
        }
      }
&.large{
  height: 380px;
}


&:first-child {
    margin-right: 7.5px;
    margin-bottom: 18px;
}

&:last-child{
    margin-left: 7.5px;
}

.background-image
{
    width: 100%;
    height: 100%;
    background-size: cover;
}

.content{
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    opacity: 0.7;
    position: absolute;
    


.title
{
    font-weight: bold;
    margin-bottom: 6px;
    font-size: 22px;
    color: #4a4a4a;
}

.subtitle{
    font-weight: lighter;
    font-size: 16px;
        }
     }
 }